
body {
  background-color: #cbd07c;
  color:#3c7160;
}

.onepiece {
  border-radius: 20px;
  border-width: 10px;
  border-color: #e06666;
  background-color:#f6e997;
  margin: 40px auto;
  padding: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  width: 75%;
  text-align: center;
  
}

h3 {
  text-align: center;
  margin: 10px;
  margin-top: -27px;
  font-size: x-large;
  text-decoration: dotted;
  text-decoration-color: #f7f4ca;
  color: #d85368;
}

input[type="text"] {
  background-color: #f7f4ca;
  width: 100px;
  text-transform: uppercase;
  color:#3c7160;
}

input[type="number"] {
  background-color:  #f7f4ca;
  width: 40px;
  color:#3c7160;

}

input[type="submit"]:focus, button:focus {
background-color:#22223b;
}



input[type="submit"],
button, div > button {
  background-color:#6a9d74;
  border: none;
  color: #f2e9e4;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 15px auto;
  margin-left: 15px;
  padding-top: 0.5%;
  padding-bottom: 0.7%;
  height: 150%;
  box-sizing: border-box;
  white-space: pre;
}

label {
  font-style: italic;
}

.menu_button {
  color:#f6e997;
  width: 20%;
  display:inline-block;
  margin: 0;
}

h1 {
  background-color: #3c7160;
  color:#d85368;
  margin: 0;
  padding: 10px;
  text-align: center;
  text-decoration-color: #22223b;
  font-family: Verdana, Geneva, Tahoma, sans-serif;

}


select {
background-color:#6a9d74;
color: #f2e9e4;

}

input[id="input"] {
  color:#3c7160;
    
}

.three_columns {
  
  column-count: 3;
  margin-left: 20%;
  margin-right: 30%;
}

.column {
    width: 20%;
    text-align: center;
    break-inside: avoid;
    margin: auto;
}



.middle_column {

    width: 5%;
    text-align: center;
    break-inside: avoid;
    margin: auto;
}

select[name="symbol"], .eq {
  margin: 0 2%;
  display: inline;
}




.two_columns{

  column-count: 2;
  text-align: center;
  margin-bottom: 20px;
  display: flex;
}

.two_columns > div {

  display: inline;
  text-align: center;
  margin: auto;
}

.buttons > div {
  display: inline-block;
  margin: auto;
  padding: auto;

}


label {
  margin-left: 5px;
}

table {

  border-collapse: collapse;
  align-self: auto;
  /* align-self: center;
  text-align: center; */

}



h4 {
  color:#d85368;
  margin-bottom: 20px;
}

.error {
  color:#d85368;
  font-size: medium;

}

.flex {

  display: grid;
  place-items: center;
  font-size: xx-large;
  padding-top: 20px;

}

.matrix {

  border-color: #6a9d74;
  border-radius: 10%;
  border-style: solid;
  text-align: center;
  margin: auto;
  min-width: 150px;
  min-height: 150px;
  display: grid;
  place-items: center;

}

.column2 {

  width: 45%;
  text-align: center;

}

form {

  align-self: center;

}

.mainmenu > button {

  width: 80%;
  height: 40px;
  border: #22223b 5px solid;
  margin: 20px;


}

